import React from "react";
// import { ReactComponent as Me } from "../me.svg";
import { ReactComponent as Me } from "../assets/me.svg";

import downloadPDF from "../utils/DownLoadPDF";
import EmailMessage from "../utils/EmailMessage";
import {
  Box,
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Toolbar,
  Tooltip,
} from "@material-ui/core";
import {
  CloudDownload,
  Work,
  Fingerprint,
  Email,
  Phone,
} from "@material-ui/icons";
import { motion } from "framer-motion";
import { makeStyles } from "@material-ui/core/styles";
import { useState } from "react";
const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
}));

function Hero() {
  const classes = useStyles();
  const [trigger, setTrigger] = useState(false);
  const copyToClipboard = () => {
    navigator.clipboard.writeText("+201006936354");
    setTrigger(true);
    setTimeout(() => {
      setTrigger(false);
    }, 2000);
  };
  return (
    <>
      <Container maxWidth="md">
        <Grid container spacing={3}>
          <Grid item xs={12} sm={4}>
            <Me style={{ width: "100%", height: "auto" }} />
          </Grid>
          <Grid item xs={12} sm={8}>
            <Container>
              <Typography variant="h2" display="block" gutterBottom>
                {" "}
                Hi there, <br /> I'm Mahmoud Abdelaziz{" "}
              </Typography>
              <Typography variant="h6" color="primary" gutterBottom>
                Full Stack JavaScript Dev
              </Typography>
              <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                startIcon={<CloudDownload />}
                onClick={downloadPDF}
              >
                download Resume
              </Button>
              <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                startIcon={<Email />}
                onClick={EmailMessage}
              >
                Say Hi
              </Button>
              <Tooltip title="Copied to clipboard" open={trigger}>
                <Button
                  variant="contained"
                  color="secondary"
                  className={classes.button}
                  startIcon={<Phone />}
                  onClick={copyToClipboard}
                >
                  Phone
                </Button>
              </Tooltip>
            </Container>
          </Grid>
        </Grid>
      </Container>
      <Toolbar />
      <Container>
        <Box paddingTop={2} paddingBottom={3}>
          {/* <motion.div
            initial={{ y: "-50px" }}
            animate={{ y: "-40px" }}
            transition={{ y: { yoyo: Infinity, duration: 1 } }}
            style={{ position: "relative" }}
          > */}
          <IconButton href="#about" color="inherit">
            <Fingerprint style={{ fontSize: "30px" }} />
          </IconButton>
          {/* </motion.div> */}
        </Box>
      </Container>
    </>
  );
}

export default Hero;
